import { Avatar, AvatarGroup, Box, Divider, ImageList, ImageListItem, List, ListItem, ListItemAvatar, ListItemText, Typography } from '@mui/material'
import React from 'react'

export const Rightbar = () => {
  return (
    <Box b flex={2} p={2} sx={{display: {xs: "none", sm: "block"}}}>
      <Box position={"fixed"} width={300}>
      <Typography variant="h6" fontWeight={100}>Online Friends</Typography>
      <AvatarGroup max={7}>
          <Avatar alt="Remy Sharp" src="https://source.unsplash.com/random/1600x900?person" />
          <Avatar alt="Travis Howard" src="https://source.unsplash.com/random/1600x901?person" />
          <Avatar alt="Cindy Baker" src="https://source.unsplash.com/random/1600x902?person" />
          <Avatar alt="Agnes Walker" src="https://source.unsplash.com/random/1600x903?person" />
          <Avatar alt="Sharon Kate" src="https://source.unsplash.com/random/1600x904?person" />
          <Avatar alt="Patricia Ron" src="https://source.unsplash.com/random/1600x905?person" />
          <Avatar alt="Lisa Ruth" src="https://source.unsplash.com/random/1600x906?person" />
          <Avatar alt="Mark Ben" src="https://source.unsplash.com/random/1600x907?person" />
      </AvatarGroup>

      <Typography variant="h6" fontWeight={100} mt={2} mb={2}>
        Latest Photos
      </Typography>
      <ImageList cols={3} rowHeight={100} gap={5}>
        <ImageListItem>
        <img
        alt=""
        src = 'https://source.unsplash.com/random/1600x907?scenery'
        />
        </ImageListItem>

        <ImageListItem>
        <img
        alt=""
        src = 'https://source.unsplash.com/random/1600x907?cake'
        />
        </ImageListItem>

        <ImageListItem>
        <img
        alt=""
        src = 'https://source.unsplash.com/random/1600x907?baby'
        />
        </ImageListItem>
      </ImageList>

      <Typography variant="h6" fontWeight={100} mt={2}>
        Latest Conversations
      </Typography>

      <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
      <ListItem alignItems="flex-start">
        <ListItemAvatar>
          <Avatar alt="Remy Sharp" src="https://material-ui.com/static/images/avatar/1.jpg" />
        </ListItemAvatar>
        <ListItemText
          primary="Brunch this weekend?"
          secondary={
            <React.Fragment>
              <Typography
                sx={{ display: 'inline' }}
                component="span"
                variant="body2"
                color="text.primary"
              >
                Ali Connors
              </Typography>
              {" — I'll be in your neighborhood doing errands this…"}
            </React.Fragment>
          }
        />
      </ListItem>
      <Divider variant="inset" component="li" />
      <ListItem alignItems="flex-start">
        <ListItemAvatar>
          <Avatar alt="Travis Howard" src="https://material-ui.com/static/images/avatar/2.jpg" />
        </ListItemAvatar>
        <ListItemText
          primary="Summer BBQ"
          secondary={
            <React.Fragment>
              <Typography
                sx={{ display: 'inline' }}
                component="span"
                variant="body2"
                color="text.primary"
              >
                to Scott, Alex, Jennifer
              </Typography>
              {" — Wish I could come, but I'm out of town this…"}
            </React.Fragment>
          }
        />
      </ListItem>
      <Divider variant="inset" component="li" />
      <ListItem alignItems="flex-start">
        <ListItemAvatar>
          <Avatar alt="Cindy Baker" src="https://material-ui.com/static/images/avatar/3.jpg" />
        </ListItemAvatar>
        <ListItemText
          primary="Oui Oui"
          secondary={
            <React.Fragment>
              <Typography
                sx={{ display: 'inline' }}
                component="span"
                variant="body2"
                color="text.primary"
              >
                Sandra Adams
              </Typography>
              {' — Do you have Paris recommendations? Have you ever…'}
            </React.Fragment>
          }
        />
      </ListItem>
    </List>
  );

    
      </Box>
    </Box>
  )
}
